import StickerWidget from "@iso/containers/Widgets/Sticker/StickerWidget";
import React from "react";
// import Alert from "@iso/ui/Antd/Alert/Alert";
import Alert from "@iso/components/Feedback/Alert";
const marginBot = {
    marginBottom: '10px',
};

// For icons, check https://react-icons.github.io/react-icons/icons?name=ai
function messageBox(text, icon, fontColor, bgColor) {
    return <StickerWidget text={<>{text}</>}
                          icon={icon}
                          fontColor={fontColor}
                          bgColor={bgColor}
    />
}

export function InfoMessageBox({text, icon}) {
    return messageBox(text, icon, "#ffffff", "#42e09f");
}

export function WarningMessageBox({text, description}) {
    // return messageBox(text, icon, "#ffffff", "#e2324d");
    return <Alert
        message={text}
        description={description}
        type="warning"
        showIcon
        // closable
        // onClose={onClose}
        style={marginBot}
    />
}

export function ErrorMessageBox({text, description}) {
    return <Alert
        message={text}
        description={description}
        type="error"
        showIcon
        // closable
        // onClose={onClose}
        style={marginBot}
    />
}
