import apiUrl from "./site.config";

export default {
    isHTML: RegExp.prototype.test.bind(/(<([^>]+)>)/i),
    hasSourceURLPort: RegExp.prototype.test.bind(/(https?:\/\/.*):(\d*)\/?(.*)/i),
    isReactLocalDevelopment: () => {
        console.log(`checking url ${apiUrl}`)
       return this.hasSourceURLPort(apiUrl)
    },
    isYoutubeVideo: RegExp.prototype.test.bind(/^((?:https?:)?\/\/)?((?:www|m)\.)?((?:youtube(-nocookie)?\.com|youtu.be))(\/(?:[\w\-]+\?v=|embed\/|v\/)?)([\w\-]+)(\S+)?$/img)
}
