import React, {Component, forwardRef} from 'react';
import {connect} from 'react-redux';
import PropTypes from "prop-types";
import {deleteRegister, fetchRegister, fetchTool, selectRegisterId} from '../../redux/lince/videoActions';
import DataTable from "react-data-table-component";
import {Trans} from 'react-i18next';
import Moment from 'react-moment';
import UserSeconds from "../utility/UserSeconds";

class LinceRegister extends Component {

    constructor(props) {
        super(props);
        this.state = {selected: null, timeOutEvent: null};//https://stackoverflow.com/questions/44845372/select-row-on-click-react-table
        this.scheduleClearSelection = this.scheduleClearSelection.bind(this);
        this._deleteScene = this._deleteScene.bind(this);
        this._playScene = this._playScene.bind(this);
    }

    scheduleClearSelection() {
        //10 sec waiting clear up
        if (this.state.timeOutEvent){
            clearTimeout(this.state.timeOutEvent);
        }
        this.setState({
            timeOutEvent: setTimeout(() => {
                this.setState({selected: null});
                this.props.selectRegisterId(null);
            }, 10000)
        });
    }

    _deleteScene(o) {
        this.props.deleteRegister({moment: o.row.videoTime, id: o.row.id});
    };

    _playScene(o) {
        /*  console.log(o.row.videoTime);
          console.log(`seconds ${o.row.videoTime} ${UserSeconds(o.row.videoTime)}`);*/
        this.setState({selected: o.row.id});
        this.props.selectRegisterId(o.row.id);
        this.props.onPlayRegister(o.row);
        this.forceUpdate();
        this.scheduleClearSelection();
    }


    componentDidMount() {
        this.props.fetchTool();
        this.props.fetchRegister();
        this._isMounted = true;
    }

    render() {
        //https://developer.aliyun.com/mirror/npm/package/react-data-table-component
        let columns = [
            {
                name: <span><i className="fa fa-odnoklassniki"/>&#160;<Trans i18nKey="action">Action</Trans></span>,
                sortable: false,
                minWidth: "108px",
                cell: row => {
                    return <div>
                        <button type="button" className="btn red btn-playscene"
                                onClick={() => this._playScene({row})}>
                            <i className="fa fa-share"/>&#160;
                        </button>
                        &#160;&#160;
                        <button type="button" className="btn blue btn-deletescene"
                                onClick={() => this._deleteScene({row})}>
                            <i className="fa fa-trash-o"/>
                        </button>
                    </div>
                }
            }, {
                name: <span><i className="fa fa-hourglass-o"/>T (sec)</span>,
                selector: 'videoTime', sortable: true, format: d => {
                    return UserSeconds(d.videoTime);
                }
            }, {
                name: <span><i className="fa fa-television"/> Frame</span>,
                hide: 'xs',
                selector: 'frames', sortable: true
            }];
        if (this.props.fullMode) {
            this.props.tool.forEach(v => {
                const currentId = v.id;
                columns.push({
                    name: <span>{v.code}</span>,
                    //sortable: true,
                    //selector: 'saveDate',
                    cell: row => {
                        let value = "";
                        row.register.forEach(currentRegister => {
                            if (currentRegister.parent === currentId) {
                                if (currentRegister.nodeInformation !== null) {
                                    value = currentRegister.nodeInformation.substring(0, 10);
                                    if (currentRegister.nodeInformation.length > 10) {
                                        value += "...";
                                    }
                                } else {
                                    value = currentRegister.code;
                                }
                            }
                        });
                        return <span>{value}</span>;
                    }
                });
            });
        }
        columns.push({
            name: <span><i className="fa fa-calendar-check-o"/>&#160;<Trans i18nKey="date">Date</Trans></span>,
            hide: 'xs',
            sortable: true,
            selector: 'saveDate',
            cell: (row) => {
                return <Moment format="DD/MM/YY HH:mm" parse="DD-MM-YYYY HH:mm:ss"
                               date={row.saveDate}/>
            }

        });
        const selectedId = this.state ? this.state.selected : -1;
        const conditionalRowStyles = [
            {
                when: row => row.id === selectedId,
                style: {
                    backgroundColor: 'rgba(248, 148, 6, 0.9)',
                    color: 'black',
                    '&:hover': {
                        cursor: 'pointer',
                    },
                },
            }
        ];
        return (
            <DataTable noHeader dense fixedHeader
                /*fixedHeaderScrollHeight="100%"*/ /*pagination*/
                       conditionalRowStyles={conditionalRowStyles}
                /*customStyles={customStyles}*/
                       columns={columns}
                       data={this.props.register}
                       defaultSortField="videoTime"
            />
        );
    }
}

LinceRegister.propTypes = {
    tool: PropTypes.array,
    register: PropTypes.array,
    fullMode: PropTypes.bool,
    activeRegister: PropTypes.number,
    fetchRegister: PropTypes.func.isRequired,
    fetchTool: PropTypes.func.isRequired,
    deleteRegister: PropTypes.func.isRequired,
    selectRegisterId: PropTypes.func.isRequired,
    onPlayRegister: PropTypes.func.isRequired,
};
LinceRegister.defaultProps = {
    fullMode: true
};
/*
forward ref https://github.com/reduxjs/react-redux/issues/914
https://buildreactapps.com/how-to-pass-ref-using-react-forwardref/
https://medium.com/flow-type/supporting-react-forwardref-and-beyond-f8dd88f35544
 */
const mapStateToProps = store => ({
    tool: store.lince.tool,
    activeRegister: store.lince.activeRegister,
    register: store.lince.register
});
const ConnectedLinceRegister = connect(mapStateToProps, {
    fetchRegister: fetchRegister,
    fetchTool: fetchTool,
    selectRegisterId: selectRegisterId,
    deleteRegister: deleteRegister
}, null, {forwardRef: true})(LinceRegister);
export default forwardRef((props, ref) =>
    <ConnectedLinceRegister {...props} myForwardedRef={ref}/>
);
