import React, {forwardRef} from 'react';
import {Col, Row} from 'antd';

/**
 * Tricky component to repair non col size info in ant design
 * Supports forwarding refs into the row
 *
 * @author Alberto Soto
 * @type {React.ComponentType<{} & React.ClassAttributes<unknown>>}
 */
const DynaRow = forwardRef((props, ref) => {
    const iterate = Array.isArray(props.children);
    const size = iterate?(24 / props.children.length) % 24:24;
    return (
        <Row ref={ref}>
            {iterate ? props.children.map((i, index) => {
                return <Col md={size} sm={size} xs={size} lg={size} key={`dynarow_${index}_${Date.now()}`}>
                    {i}
                </Col>
            }) : <Col md={size} sm={size} xs={size} lg={size}>{props.children}</Col>}
        </Row>

    );
});
export default DynaRow;