import React, {Component, createRef} from 'react';
import {connect} from 'react-redux';
import {Col, Collapse, Layout, Row} from 'antd';
import PageHeader from '@iso/components/utility/pageHeader';
import {Provider} from 'react-redux';
import {store} from '../../redux/store';
import '../../config/i18n';
import LinceRegister from '../../components/lince/LinceRegister';
import IntlMessages from "@iso/components/utility/intlMessages";
import siteConfig from '../../config/site.config';
import ReactGA from 'react-ga';
import VideoAI from "../../components/lince/video/VideoAI";
import StyledResizable from "../../components/lince/StyledResizable";
import LinceVideoTool from "../../components/lince/LinceVideoTool";
import {baseUrl, fetchVideos} from "../../redux/lince/videoActions";
import PropTypes from "prop-types";
import RegexConfig from "../../config/regex.config";
import regexConfig from "../../config/regex.config";
import ContentHolder from "@iso/components/utility/contentHolder";
import Box from "@iso/components/utility/box";
import {InfoMessageBox, WarningMessageBox} from "../../components/utility/MessagePrompter";

ReactGA.initialize(siteConfig.google.analyticsKey);
ReactGA.pageview(window.location.pathname + window.location.search);

class AIStudioApp extends Component {

    constructor(props) {
        super(props);
        this.state = {
            date: new Date(),
            tool: {},
            video: "/climbing.mp4"
        };
        this.linceTool = createRef();
        this.linceRegister = createRef();
        this.innerPlayer = createRef();
        this._doRegisterPlay = this._doRegisterPlay.bind(this);
        this._doSave = this._doSave.bind(this);
        this.renderVideoComponent = this.renderVideoComponent.bind(this);
    }

    componentDidMount() {
        this.props.fetchVideos();
    }

    _doRegisterPlay(o) {
        if (o && o.videoTime) {
            this.linceTool.current.loadSceneData(o);
            this.innerPlayer.current.doPlayScene(o.videoTime);
        }
    }

    _doSave() {
        let time = this.innerPlayer.current.getCurrentTime();
        this.linceTool.current.remoteSave(time);
    }

    /**
     * Supports self-configuration to allow local player development and sets different view on
     * url set according to site configuration
     *
     * @param videos the current lince loaded player.
     * @returns {Element} VideoAI player
     */
    renderVideoComponent(videos) {
        const firstVideo = videos && videos.length > 0 ? videos[0] : null;
        const isReactLocalDevelopment = RegexConfig.hasSourceURLPort(baseUrl)
        if (!firstVideo) {
            return <Box title={"Video Player"}>
                <ContentHolder>
                    <WarningMessageBox text={<IntlMessages id="translations:video-empty"/>} icon={"ion-wand"}/>
                </ContentHolder>
            </Box>
        }
        if (regexConfig.isYoutubeVideo(firstVideo.url)) {
            return <Box title={"Video Player"}>
                <ContentHolder>
                    <WarningMessageBox text={<IntlMessages id="youtube-ai"/>} icon={"ion-wand"}/>
                </ContentHolder>
            </Box>
        }
        if (isReactLocalDevelopment) {
            return <VideoAI ref={this.innerPlayer} videoSource={"/climbing.mp4"}/>
        } else {
            if (firstVideo && firstVideo.url) {
                return <VideoAI ref={this.innerPlayer} videoSource={baseUrl + firstVideo.url}/>
            }
        }
    }

    render() {
        return (
            <Provider store={store}>
                <Layout>
                    <PageHeader><IntlMessages id="ai-studio-title"/></PageHeader>
                    <Row>
                        <Col md={24} style={{paddingBottom: "5px"}}>
                            <InfoMessageBox text={<>
                                <IntlMessages id="experimental-option"/>.
                                <IntlMessages id="experimental-tool"/>
                            </>} icon={"ion-wand"}/>
                        </Col>
                    </Row>
                    <div style={{display: 'flex', width: '100%', height: '100%'}}>
                        <StyledResizable defaultSize={{width: '65%', height: '100%'}} minWidth="300" minHeight="600">
                            <Row style={{minHeight: "400px"}}>
                                <Col span={24} style={{height: "auto"}}>
                                    {this.renderVideoComponent(this.props.videos)}
                                </Col>
                            </Row>
                            <Row>
                                <Col span={24}>
                                    <LinceRegister ref={this.linceRegister} onPlayRegister={this._doRegisterPlay}/>
                                </Col>
                            </Row>
                        </StyledResizable>
                        <div style={{width: "100%"}}>
                            <LinceVideoTool ref={this.linceTool} tool={this.state.tool} onSave={this._doSave}/>
                        </div>
                    </div>
                </Layout>

            </Provider>
        );
    }
}

AIStudioApp.propTypes = {
    videos: PropTypes.array.isRequired,
    fetchVideos: PropTypes.func.isRequired,
};
const mapStateToProps = state => ({
    videos: state.lince.videos
});
export default connect(mapStateToProps, {fetchVideos}, null, {forwardRef: true})(AIStudioApp);
