class CategoryButton extends Component {

    constructor(props) {
        super(props);
        this.state = {active: false};
        this._doSelect = this._doSelect.bind(this);
        this.setStatusTo = this.setStatusTo.bind(this);
        this.isActive = this.isActive.bind(this);
    }

    _doSelect(cat) {
        //console.log(this.props.category);
        this.props.onSelect(this.props.category);
    }

    setStatusTo(b) {
        this.setState({active: b});
    }

    isActive() {
        return this.state.active;
    }

    render() {
        const cat = this.props.category;
        const baseClass = "icon-btn btn-cri cat-group-" + cat.parent;
        const addClass = baseClass + (this.state.active ? " bg-yellow-crusta bg-font-yellow-crusta" : "");
        const overText = cat.name + (cat.description ? ": " + cat.description : "");
        return (
            <div className={addClass}>
                <a key={cat.id} className={addClass} onClick={(cat) => this._doSelect(cat)}
                    /*id={cat.id}*/
                   title={overText}
                   href={`#${cat.id}`}
                   data-code={cat.code} data-id={cat.id} data-group={cat.name} style={{width: "100%"}}>
                    <svg viewBox="0 0 50 50" width="100%" height="40">
                        <text x="0" y="32">{cat.code}</text>
                        {/*transform="rotate(45 30,20)"*/}
                    </svg>
                </a>
            </div>
        )
    }
}

CategoryButton.propTypes = {
    category: PropTypes.object,
    onSelect: PropTypes.func,
};

class LinceVideoTool extends Component {
    constructor(props) {
        super(props);
        this.state = {canSave: false, registerId: null, moment: 0.1, categories: [], nonCollapsed: []};//lo situamos por defecto al inicio
        this._onSelectCategory = this._onSelectCategory.bind(this);
        this._handleCollapse = this._handleCollapse.bind(this);

        this._renderCriteria = this._renderCriteria.bind(this);
        this._unselectNotChosen = this._unselectNotChosen.bind(this);
        this.loadSceneData = this.loadSceneData.bind(this);
        this.getToolState = this.getToolState.bind(this);
        this.setTime = this.setTime.bind(this);
        this.remoteSave = this.remoteSave.bind(this);
        this.onSave = this.props.onSave.bind(this);
    }

    //Este debe notificar a false a los que no son persistence
    remoteSave(time) {
        // console.log(this.props.activeRegister);
        let categories = [];//{code:cat.code}
        //recorremos todos los componentes y aadimos los que estn activos al array
        this.props.tool.forEach((v) => {
            try {
                if (v.informationNode) {
                    //key={crit.id + "-row-info"}
                    const inputValue = this.refs[v.id + "-row-info"].value;
                    categories.push({id: v.id, code: v.code + "-DATA", nodeInformation: inputValue});
                } else {
                    v.innerCategories.forEach((k) => {
                        if (this.refs[k.id + "cat" + k.code].state.active) {
                            categories.push({id: k.id, code: k.code});
                        }
                    });
                }
            } catch (e) {
                console.log(`No criteria value for id ${v.id}, code ${v.code} & name ${v.name}`);
            }
        });
        this.setTime(time);
        this.setState({categories: categories});
        let aux = {
            moment: time,
            categories: categories,
            id: this.props.activeRegister
        };
        this.props.saveRegister(aux);
        this._unselectNotPersistence();
    }

    clearInputs() {
        this.props.tool.forEach((t) => {
            try {
                if (t.informationNode) {
                    this.refs[t.id + "-row-info"].value = "";
                }
            } catch (e) {

            }
        });
        this.setCurrentSceneId(null);
    }

    loadSceneData(data) {
        // console.log(data);
        // console.log("selected Register Id in props:" + this.props.activeRegister);
        //para todos los registros
        this.clearInputs();
        this._unselectNotPersistence(true);
        data.register.forEach((register) => {
            //recorremos la herramienta
            this.props.tool.forEach((t) => {
                try {
                    if (t.id && register.parent === t.id) {
                        if (t.informationNode) {
                            // console.log(t);
                            // console.log("tocado:"+ textValue);
                            this.refs[t.id + "-row-info"].value = register.nodeInformation;
                        } else {
                            //hacemos el set de esa fila para cada boton: activo o no
                            t.innerCategories.forEach((cat) => {
                                const status = (register.code === cat.code);
                                const elem = this.refs[cat.id + "cat" + cat.code];
                                if (elem) {
                                    elem.setStatusTo(status);
                                    this.setState({[cat.parent]: cat.code});
                                }
                            });
                        }
                    }
                } catch (e) {
                    console.log("loading rule err " + e);
                }
            });
        });
        this.setTime(data.videoTime);
        this.setCurrentSceneId(this.props.activeRegister);
    }

    setTime(time) {
        this.setState({moment: time});
    }

    setCurrentSceneId(id) {
        this.setState({registerId: id});
    }

    /**
     * Unchecks all items that are not persistant
     * @private
     */
    _unselectNotPersistence(forceAll = false) {
        this.props.tool.forEach((v) => {
            /*      console.log("unselected");
                  console.log(v);*/
            if (v.persistence === false || forceAll) {
                v.innerCategories.forEach((k) => {
                    const elem = this.refs[k.id + "cat" + k.code];
                    if (elem) {
                        elem.setStatusTo(false);
                    }
                });
                if (v.informationNode) {
                    const elem = this.refs[v.id + "-row-info"];
                    if (elem) {
                        elem.value = "";
                    }
                }
            }
        });
    }

    _unselectNotChosen(parent, target) {
        //console.log("unselectnotchosen init:" + target);
        let buttonIsSet = true;
        this.props.tool.forEach((v) => {
            if (v.id && parent === v.id) {
                v.innerCategories.forEach((k) => {
                    let status = (target === k.code);
                    if (status) {
                        //console.log("encontrado" + k.code);
                        if (this.refs[k.id + "cat" + k.code].isActive()) {
                            //Se tiene que desactivar
                            status = false;
                            buttonIsSet = false;
                        }
                    }
                    this.refs[k.id + "cat" + k.code].setStatusTo(status);
                    /*     if (k.informationNode) {
                             this.refs[k.id + "-row-info"].value = "";
                         }*/
                });

            }
        });
        return buttonIsSet;
    }

    _onSelectCategory(cat) {
        //this.setState({[cat.parent]: cat.code}); Puede ser asyncrono
        // https://reactjs.org/docs/state-and-lifecycle.html
        //console.log(cat);
        this.setState((state, props) => {
            //La clave esta aqui. Si cat.code es null no seleccionar ninguno
            const hasBeenChange = this._unselectNotChosen(cat.parent, cat.code);
            //console.log("click:" + JSON.stringify({[cat.parent]: hasBeenChange ? cat.code : ""}));
            return {[cat.parent]: hasBeenChange ? cat.code : ""}
        });
        this.setState({canSave: true});
    }

    getToolState() {
        console.log(this.state);
    }

    componentDidMount() {
        this.props.fetchTool();
    }

    /**
     * Clones the initial array of data, and sends a new set matrix with custom size
     * Funny way to split a component by row in React.
     * Crazy!
     * @param crit criteria
     * @returns {Array} matrix of innerCategories
     * @private
     */
    _splitInnerCategories(crit) {
        let rows = [], size = this.props.rowSize;
        const catContainer = crit.innerCategories.slice();//gens new copy, not reference
        /*   console.log("------- init");
           console.log(crit);
           console.log(catContainer);*/
        //let's consume the copy
        while (catContainer.length > 0) {
            rows.push(catContainer.splice(0, size));
        }
        /*   console.log(catContainer);
           console.log("------- end");*/
        //console.log(rows);
        return rows;
    }

    _renderButtonRows(crit, rows) {
        if (crit.informationNode) {
            return (<DynaRow>
                <input ref={crit.id + "-row-info"} key={crit.id + "-row-info"} className={"ant-input "}
                       placeholder="Enter text"/>
            </DynaRow>);
        } else {
            return (<>{
                    rows.map((rowItem, index) => {
                        return <div className={"lince-flex-container"} key={crit.id + "-row" + index}>
                            {
                                rowItem.map((cat) => {
                                    return <CategoryButton category={cat} ref={cat.id + "cat" + cat.code}
                                                           key={cat.id + "cat" + cat.code}
                                                           onSelect={(cat) => this._onSelectCategory(cat)}/>
                                })
                            }
                        </div>
                    })
                }</>
            );
        }
    }

    //m-grid-responsive-xs
    _renderCriteria(crit) {
        let rows = this._splitInnerCategories(crit);
        let innerButtonRow = this._renderButtonRows(crit, rows);//<div className="m-grid-row"></div>
        let persistancePin = "";
        if (crit.persistence)
            persistancePin = <i className="ion-pin"/>;
        return (
            <Row className="center-block" key={"cri" + crit.id} justify="space-around">
                <Col span={24}>
                    <Card style={{width: "100%", marginTop: 5}}>
                        <Meta
                            title={<h3 className={"linceFont"}>{`${crit.name} `}{persistancePin}</h3>}/>
                        {innerButtonRow}
                    </Card>
                    {/* ${crit.code} -  <h4 className="text-center bg-yellow-gold bg-font-yellow-gold">
                            <span>{crit.name}</span>
                            {persistancePin}
                        </h4>*/}
                </Col>
            </Row>
        )
    }

    /**
     * Render buttons with adjusting rows to content. Quite tricky
     * @param crit
     * @returns {XML}
     * @private
     */
    _renderCollapsableCriteria(crit) {
        let rows = this._splitInnerCategories(crit);
        let innerButtons = this._renderButtonRows(crit, rows);
        return (
            <div key={"cri" + crit.id}>
                {innerButtons}
            </div>
        )
    }

    _handleCollapse(item) {
        this.setState({nonCollapsed: item});
    }

    _keyboardActions(key, e) {

        switch (key) {
            case keyboardConfig.saveActionKey:
                this.onSave();
                showSuccess("Escena guardada", "");
                break;
            default:
                break;
        }
        console.log(`do something upon keydown event of ${key}`);
        return false;
    }

    render() {
        const toolData = this.props.tool;
        const showButton = "pull-left " + this.state.canSave ? "" : "hidden";
        let toolDataRender, collapseActions = "";
        if (toolData.length > this.props.collapseSize) {
            let arrkeys = [];
            toolDataRender = (<LinceCollapse defaultActiveKey={['1']} activeKey={this.state.nonCollapsed}
                                             onChange={this._handleCollapse}>
                {toolData.map((v) => {
                    let persistancePin = "";
                    const panelKey = `panel_${v.id}`;
                    arrkeys.push(panelKey);
                    if (v.persistence)
                        persistancePin = <i className="ion-pin"/>;
                    return <Panel header={<span>{persistancePin} {v.name}</span>} key={panelKey}>
                        {this._renderCollapsableCriteria(v)}
                    </Panel>
                })}
            </LinceCollapse>);
            const menu = (
                <Menu>
                    <Menu.Item>
                        <Button onClick={() => this.setState({nonCollapsed: arrkeys})}>
                            Desplegar todos
                        </Button>
                    </Menu.Item>
                    <Menu.Item>
                        <Button onClick={() => this.setState({nonCollapsed: []})}>Cerrar todos</Button>
                    </Menu.Item>
                </Menu>
            );
            collapseActions = <Dropdown overlay={menu}>
                <a className="ant-dropdown-link" onClick={e => e.preventDefault()} href={"#"}>
                    Acciones <DownOutlined/>
                </a>
            </Dropdown>
        } else {
            toolDataRender = toolData.map((v) => this._renderCriteria(v))
        }
        const scrollbarStyles = {borderRadius: 5};
        return (
            <LincePanelWrapper>
                <KeyboardEventHandler
                    handleKeys={this.props.keyboard}
                    onKeyEvent={(key, e) => this._keyboardActions(key, e)}/>
                <LayoutContent className={"minPadding"}>
                    <Row>
                        <Col md={5} xs={7}>
                            <Button type="primary" className={`isoAddNoteBtn ${showButton}`} size={"small"}
                                    onClick={() => this.onSave()}>
                        <span className="label label-warning">
                            <i className="fa fa-floppy-o" aria-hidden="true"/>&#160;
                            {/* <Trans i18nKey="save">Save</Trans>*/}
                            <IntlMessages id="save"/>
                        </span>
                            </Button>
                        </Col>
                        <Col md={14} xs={10}>
                            {/*<h2><Trans i18nKey="video.headerTool">Criteria & Categories</Trans></h2>*/}
                            <h2 style={{paddingLeft: "12px"}}><IntlMessages id="criteria-&-categories"/></h2>
                        </Col>
                        <Col md={5} xs={7}>
                            {collapseActions}
                        </Col>
                    </Row>
                    <Row>
                        <Col span={24} style={{marginTop: "5px", marginBottom: "5px"}}>
                            <ScrollArea speed={0.8} className="area" contentClassName="content" horizontal={false}
                                        verticalScrollbarStyle={scrollbarStyles}
                                        verticalContainerStyle={scrollbarStyles}
                                        smoothScrolling={true}>
                                {toolDataRender}
                            </ScrollArea>
                        </Col>
                    </Row>
                    <Row>
                        <Col span={12}>
                            <Button type="primary" className={`isoAddNoteBtn ${showButton}`} size={"large"}
                                    onClick={() => this.onSave()}>
                        <span className="label label-warning">
                        <i className="fa fa-floppy-o" aria-hidden="true"/>&#160;
                            {/*<Trans i18nKey="save">Save</Trans>*/}
                            <IntlMessages id="save"/>
                        </span>
                            </Button>
                        </Col>
                    </Row>
                </LayoutContent>
            </LincePanelWrapper>
        );
    }
}

LinceVideoTool.defaultProps = {
    collapseSize: 5,
    rowSize: 6,
    tool: {data: [{}]},
    keyboard: [keyboardConfig.saveActionKey]
};
LinceVideoTool.propTypes = {
    collapseSize: PropTypes.number.isRequired,
    rowSize: PropTypes.number.isRequired,
    tool: PropTypes.array.isRequired,
    activeRegister: PropTypes.number,
    fetchTool: PropTypes.func.isRequired,
    saveRegister: PropTypes.func.isRequired,
    onSave: PropTypes.func,
    keyboard: PropTypes.array.isRequired,
};
const mapStateToProps = store => ({
    tool: store.lince.tool,
    activeRegister: store.lince.activeRegister,
});

export default connect(mapStateToProps, {
    fetchTool: fetchTool,
    saveRegister: saveRegister
}, null, {forwardRef: true})(LinceVideoTool);
